.navbar {
  height: 74px;
  padding: 14px 80px;
  background-color: #ffffff;

  @media screen and (max-width: 1199px) {
    padding: 14px 0px;
  }
}

header {
  .nav {
    &-item {
      padding-left: 7px;
      padding-right: 7px;

      @media screen and (max-width: 1100px) {
        padding-left: 3px;
        padding-right: 3px;
      }

      @media screen and (max-width: 991px) {
        padding-left: 7px;
        padding-right: 7px;
      }
    }

    &-link {
      font-weight: normal;
      font-size: 24px;
      line-height: 30px;
      color: $dark-blue;

      &.active {
        text-decoration: line-through;
      }
    }
  }
}
