  border-radius: 100px; }

.error {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 74px); }
  .error__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 358px;
    height: 309px;
    margin-bottom: 34px;
    padding: 35px;
    border-radius: 15px;
    background-color: #E27676; }
  .error__code {
    font-weight: 600;
    font-size: 72px;
    line-height: 91px;
    color: #fff; }
  .error__message {
    margin-bottom: 19px;
    font-weight: normal;
    font-size: 48px;
    line-height: 43px;
    color: #000; }
  .error__text {
    margin-bottom: 48px;
    font-weight: normal;
    font-size: 24px;
    line-height: 22px;
    color: #000; }
  .error__link {
    font-weight: normal;
    font-size: 24px;
    line-height: 22px;
    text-decoration: underline;
    color: #000; }
    .error__link:hover {
      text-decoration: underline !important; }
